        <el-form-item label="参与门店" required>
          <!-- 选择参与门店  线上 -->
          <el-button size="small" @click="chooseStoreOpen" icon="el-icon-plus" v-if="addParamMesg.shopType == 1" :disabled="status === 3 || status === 2">选择门店</el-button>
          <div v-if="addParamMesg.shopType == 1 && activityStoresCheckedList.length > 0" class="store-check-wraper">
            <el-table
              class="fixed-height"
