              </el-table-column>
            </el-table>
          </div>
          <!-- 选择参与门店  线下 -->
          <el-button size="small" @click="chooseOfflineStoreOpen" icon="el-icon-plus" v-if="addParamMesg.shopType == 2" :disabled="status === 3 || status === 2">选择门店</el-button>
          <div v-if="addParamMesg.shopType == 2 && offlineStoresCheckedList.length > 0" class="store-check-wraper">
            <el-table
              class="fixed-height"
