              </el-table-column>
              <el-table-column min-width="100px" prop="spec" label="规格"></el-table-column>
              <!-- 选择参与商品  线上 -->
              <el-table-column min-width="90px" prop="storeMinPrice" label="门店最低价" v-if="addParamMesg.shopType == 1"></el-table-column>
              <el-table-column min-width="150px" label="限购次数(为空不限)">
                <template slot-scope="scope">
                  <div>
                    <el-input v-model="scope.row.limitGoodsCount" size="small" placeholder="输入限购次数" :disabled = "scope.row.disabled"></el-input>
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="110px" label="活动库存">
                <template slot-scope="scope">
                  <div>
                    <el-input v-model="scope.row.curStock" size="small" placeholder="输入库存" :disabled = "scope.row.disabled"></el-input>
                  </div>
                </template>
              </el-table-column>
              <el-table-column
                label="活动规则"
                fixed="right"
                width="80">
                <template slot-scope="scope" size="small">
                  <el-button type="text" size="mini" icon="el-icon-setting" @click="setDiscount(scope.row)" :disabled = "scope.row.disabled">设置</el-button>
                </template>
              </el-table-column>
              <el-table-column
                label="操作"
                fixed="right"
                width="70">
                <template slot-scope="scope" size="small">
                  <el-button @click="deleteGoodsItem(scope.row, addParamMesg.shopType)" type="text" size="mini" icon="el-icon-delete" :disabled = "scope.row.disabled">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div v-if="addParamMesg.shopType == 1">
              <span style="color: red;">*</span>注意：权重排序，先创建的活动商品在前；剩余的按前端规则走。
            </div>
            <div v-if="addParamMesg.shopType == 2">
              <span style="color: red;">*</span>注意：如遇特殊情况（pos机断网等），可能导致限购次数和活动库存不准。
            </div>
          </div>
        </el-form-item>
        <hr class="s-slip-tb">
        <el-form-item>
          <el-button type="primary" size="small" @click="doSaveHandel" :disabled="isDisabled">保存</el-button>
          <el-button size="small" @click="doCancelHandel">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  
    <!-- 选择门店 包装折扣商城-->
    <choose-store ref="chooseStore" @confirmChooseStore="confirmChooseStore"></choose-store>
    <!-- 选择商品 包装折扣商城-->
    <choose-goods ref="chooseGoods" @confirmChooseGoods="confirmChooseGoods"></choose-goods>
    <!-- 选择门店  包装折扣门店-->
    <choose-offline-store ref="chooseOfflineStore" @confirmChooseStore="confirmChooseOfflineStore"></choose-offline-store>
    <!-- 选择门店商品 包装折扣门店-->
    <choose-offline-goods ref="chooseOfflineGoods" @confirmChooseOfflineGoods="confirmChooseOfflineGoods"></choose-offline-goods>
    
    <el-dialog title="设置活动规则" :visible.sync="setDisDialogShow" width='440px' style="padding-top: 0;" :close-on-click-modal="false" :close-on-press-escape="false">
      <div>
        <div class="set-goods-table-tr">
          <div class="set-goods-table-th tr1">商品信息</div>
          <div class="set-goods-table-th tr2">规格</div>
          <div class="set-goods-table-th tr3" v-if="addParamMesg.shopType == 1">零售价</div>
        </div>
        <div class="set-goods-table-tr">
          <div class="set-goods-table-td tr1">
            <div class="ll-p-m-w" v-if="addParamMesg.shopType == 1">
              <div class="ll-img-w">
                <img v-if="setItemDiscountMsg.imgUrl && setItemDiscountMsg.imgUrl != ''" :src="setItemDiscountMsg.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                <img v-else src="/shop/static/img_error.png" alt="">
              </div>
              <div class="ll-p-m-c-r">
                <div>
                  {{setItemDiscountMsg.goodsName || '-'}}
                </div>
                <div class="ll-erp-p-no">SKU编码：{{setItemDiscountMsg.goodsNo || '-'}}</div>
              </div>
            </div>
            <div class="ll-p-m-w" v-if="addParamMesg.shopType == 2">
              <div class="ll-p-m-c-r">
                <div>
                  {{setItemDiscountMsg.name || '-'}}
                </div>
                <div class="ll-erp-p-no">ERP编码：{{setItemDiscountMsg.goodsNo || '-'}}</div>
              </div>
            </div>
          </div>
          <div class="set-goods-table-td tr2">{{setItemDiscountMsg.spec || '-'}}</div>

          <div class="set-goods-table-td tr3" v-if="addParamMesg.shopType == 1">{{setItemDiscountMsg.price}}</div>
        </div>

        <div class="set-dis-msg-list-wrap">
          <div class="set-dis-msg-item" v-for="(item, idx) in setItemDiscountMsg.activityDiscounts" style="margin-top: 10px;" :key="idx">
            <el-input v-model="item.qty" size="small" placeholder="请输入" class="search-input-small-width"></el-input>
            <span style="padding: 0 5px; color: #222;">份</span> 
            <el-input v-model="item.amount" size="small" placeholder="请输入" class="search-input-small-width"></el-input>
            <span style="padding: 0 5px; color: #222;">元</span>
            <el-button type="primary" icon="el-icon-plus" circle size="mini" class="sup-mini" @click="addDisItems(idx)"></el-button>
            <el-button type="primary" icon="el-icon-minus" circle size="mini" class="sup-mini" @click="removeDisItems(idx)" v-if="idx !== 0"></el-button>
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="setDisDialogShow = false" size="mini">取消</el-button>
        <el-button type="primary" size="mini" @click="doSetDisSaveHandel(setItemDiscountMsg.goodsNo)">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import ChooseStore from "@/components/Activity/Common/ChooseStore"
  import ChooseGoods from "@/components/Activity/Common/ChooseGoodsFromLib"
  import ChooseOfflineStore from "../../DisCoupon/Dialog/ChooseStore"
  import ChooseOfflineGoods from "../../DisCoupon/Dialog/ChooseOfflineGoods"
  const reqUrl = {
    // 活动规则编辑跳转
    edit: "/plateform/a/activityRule/edit",
    // 修改活动规则
    updateDiscount: "/plateform/a/activityRule/updateDiscount",
    // 新增活动规则
    addDiscount: "/plateform/a/activityRule/addDiscount"
  }
  export default {
    data() {
      return {
        status: 0,
        optType: '', // 此页面操作类型  edit  编辑   copy  复制
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7; // 如果没有后面的-8.64e7就是不可以选择今天的
          }
        }, 
        isDisabled: false,
        addParamMesg: {
          DIFFCITY: true,
          platformId: 5,
          regionCode: '',
          regionName: '',
          id: '',
          activityId: '', // 活动id
          activityType: '', // 活动类型
          activityTypeName: '', // 活动类型名称
          shopType: '', // 商城类型 ：1： 商城，2：门店
          shopTypeName: '', 
          isMember: 1, // 是否是会员 ：0：全部，1： 是，2：否
          activityName: '', // 活动名称
          activityStartDate: '', // 活动开始时间
          activityEndDate: '', // 活动结束时间
          activityCode: '', // 活动编码
          // 参与门店
          activityStoreList: [],
          // 活动商品集合
          platCityGoodsInfos: []
        },
        // 参与门店选中的  线上
        activityStoresCheckedList: [],
        // 参与的选中的商品  线下
        activityGoodsCheckedList: [],

        // 参与门店选中的 线下
        offlineStoresCheckedList: [],
        // 参与的选中的商品 线下
        offlineGoodsCheckedList: [],

        // 设置单条
        setItemDiscountMsg: {},
        // 设置弹窗
        setDisDialogShow: false

      }
    },
    beforeRouteEnter(to, from, next) {
      if(to.params.type === 'edit') {
        to.meta.breadcrumbList = [
          {name: '营销活动', path: ''},
          {name: '店铺活动', path: '/store_act'},
          {name: '编辑活动', path: ''}
        ]
      }else if(to.params.type === 'copy') {
        to.meta.breadcrumbList = [
          {name: '营销活动', path: ''},
          {name: '店铺活动', path: '/store_act'},
          {name: '复制活动', path: ''}
        ]
      }
      next()
    },
    created() {
      let activityId = this.$route.params.activityId
      this.addParamMesg.activityId = activityId
      this.optType = this.$route.params.type
      this.status = this.$route.query.status ? parseInt(this.$route.query.status) : 0
      this.getDetail()
    },
    components: {
      "choose-store": ChooseStore,
      "choose-goods": ChooseGoods,
      "choose-offline-store": ChooseOfflineStore,
      "choose-offline-goods": ChooseOfflineGoods
    },
    methods: {
      /**
       * [getDetail 获取拼团活动详情]
       * @return {[type]} [description]
       */
      getDetail() {
        let activityId = this.addParamMesg.activityId || ''
        utils.showLoading()
        this.$http.post(reqUrl.edit, {activityId: activityId, DIFFCITY: true}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data) {
              let createGoodsMesg = res.data
              this.addParamMesg.id = createGoodsMesg.id
              this.addParamMesg.activityId = createGoodsMesg.id
              this.addParamMesg.regionCode = createGoodsMesg.regionCode || ''
              this.addParamMesg.regionName = createGoodsMesg.regionName || ''
              this.addParamMesg.activityType = createGoodsMesg.activityType || ''
              this.addParamMesg.activityTypeName = createGoodsMesg.activityTypeName || ''
              this.addParamMesg.activityName = createGoodsMesg.activityName || ''
              this.addParamMesg.activityStartDate = createGoodsMesg.activityStartDate || ''
              this.addParamMesg.activityEndDate = createGoodsMesg.activityEndDate || ''
              this.addParamMesg.activityCode = createGoodsMesg.activityCode || ''
              this.addParamMesg.isMember = createGoodsMesg.isMember ? parseInt(createGoodsMesg.isMember) : 0
              this.addParamMesg.shopType = createGoodsMesg.shopType || ''
              this.addParamMesg.shopTypeName = createGoodsMesg.shopTypeName || ''

              if(createGoodsMesg.storeVos && createGoodsMesg.storeVos.length) {
                if(createGoodsMesg.shopType === 1) {
                  this.activityStoresCheckedList = createGoodsMesg.storeVos
                }else if(createGoodsMesg.shopType === 2) {
                  for(let item of createGoodsMesg.storeVos) {
                    item.storeNo = item.erpStoreNo
                    item.storeName = item.erpStoreName
                    item.managementTypeCode = item.saleType
                    item.managementTypeValue = item.saleTypeName
                  }
                  this.offlineStoresCheckedList = createGoodsMesg.storeVos
                }
              }
              if(createGoodsMesg.platCityGoodsVos && createGoodsMesg.platCityGoodsVos.length) {
                if(createGoodsMesg.shopType === 1) {
                  createGoodsMesg.platCityGoodsVos.forEach(item => {
                    item.disabled = this.status === 3 || this.status === 2 ? true : false
                  })
                  this.activityGoodsCheckedList = createGoodsMesg.platCityGoodsVos
                }else if(createGoodsMesg.shopType === 2) {
                  for(let item of createGoodsMesg.platCityGoodsVos) {
                    item.disabled = this.status === 3 || this.status === 2 ? true : false
                    item.name = item.activityGoodsName
                    item.categoryNoName = item.categoryName
                  }
                  this.offlineGoodsCheckedList = createGoodsMesg.platCityGoodsVos
                }
              }

            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [doSaveHandel 保存]
       * @return {[type]} [description]
       */
      doSaveHandel() {
        let numReg = /^[1-9]*[1-9][0-9]*$/
        let salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
        let salePriceOnePointReg = /((^[1-9]\d*))(\.\d{0,1}){0,1}$/

        if(!this.addParamMesg.activityName) {
          this.$message("请输入活动名称")
          return
        }else if(this.addParamMesg.activityName.length > 30) {
          this.$message("活动名称限制30个汉字")
          return
        }
        if(!this.addParamMesg.activityStartDate) {
          this.$message("请输入活动开始时间")
          return
        }
        if(!this.addParamMesg.activityEndDate) {
          this.$message("请输入活动结束时间")
          return
        }
        let activityStartDateNum = Date.parse(new Date(this.addParamMesg.activityStartDate))
        let activityEndDateNum = Date.parse(new Date(this.addParamMesg.activityEndDate))
        let currentDateNum = new Date().getTime()
        if(activityStartDateNum < currentDateNum) {
          this.$message("活动开始时间不能早于当前时间")
          return
        }
        if(activityStartDateNum > activityEndDateNum) {
          this.$message("活动结束时间不能早于开始时间")
          return
        }
        this.addParamMesg.activityStoreList = []
        this.addParamMesg.platCityGoodsInfos = []
        if(this.addParamMesg.shopType == 1) {
          // 商城 门店  线上
          if(this.activityStoresCheckedList && this.activityStoresCheckedList.length) {
            for(let item of this.activityStoresCheckedList) {
              this.addParamMesg.activityStoreList.push(item.erpStoreNo)
            }
          }
          // 商城 商品  线上
          if(this.activityGoodsCheckedList && this.activityGoodsCheckedList.length) {
            for(let item of this.activityGoodsCheckedList) {
              if(item.limitGoodsCount) {
                if(!numReg.test(item.limitGoodsCount)) {
                  this.$message(`(${item.goodsName})的限购次数为大于零的正整数`)
                  return
                }
              }
              if(!item.curStock) {
                this.$message(`请输入(${item.goodsName})的活动库存`)
                return
              }else if(!numReg.test(item.curStock)) {
                this.$message(`(${item.goodsName})的活动库存为大于零的正整数`)
                return
              }
              if(item.activityDiscounts && item.activityDiscounts.length) {
                for(let j of item.activityDiscounts) {
                  if(!j.qty || !j.amount) {
                    this.$message(`请设置(${item.goodsName})的活动规则`)
                    return
                  }
                }
              }else {
                this.$message(`请设置(${item.goodsName})的活动规则`)
                return
              }
              let itemMesg = {
                activityStartTime: this.addParamMesg.activityStartDate.split(' ')[1],
                activityEndTime: this.addParamMesg.activityEndDate.split(' ')[1],
                activityDiscounts: item.activityDiscounts,
                activityGoodsName: item.goodsName,
                curStock: item.curStock,
                platformCityGoodsId: item.platformCityGoodsId,
                sortNum: '',
                activityStartDate: this.addParamMesg.activityStartDate,
                activityEndDate: this.addParamMesg.activityEndDate,
                activityId: this.addParamMesg.activityId,
                categoryCode: item.categoryId,
                categoryName: item.categoryName,
                goodsNo: item.goodsNo,
                limitGoodsCount: item.limitGoodsCount,
                platformCityGoodsName: item.goodsName,
                spec: item.spec
              }
              this.addParamMesg.platCityGoodsInfos.push(itemMesg)
            }
          }
        }else {
          // 门店  线下
          if(this.offlineStoresCheckedList && this.offlineStoresCheckedList.length) {
            for(let item of this.offlineStoresCheckedList) {
              this.addParamMesg.activityStoreList.push(item.storeNo)
            }
          }
          // 商城 商品  线下
          if(this.offlineGoodsCheckedList && this.offlineGoodsCheckedList.length) {
            for(let item of this.offlineGoodsCheckedList) {
              if(item.limitGoodsCount) {
                if(!numReg.test(item.limitGoodsCount)) {
                  this.$message(`(${item.name})的限购次数为大于零的正整数`)
                  return
                }
              }
              if(!item.curStock) {
                this.$message(`请输入(${item.name})的活动库存`)
                return
              }else if(!numReg.test(item.curStock)) {
                this.$message(`(${item.name})的活动库存为大于零的正整数`)
                return
              }
              if(item.activityDiscounts && item.activityDiscounts.length) {
                for(let j of item.activityDiscounts) {
                  if(!j.qty || !j.amount) {
                    this.$message(`请设置(${item.name})的活动规则`)
                    return
                  }
                }
              }else {
                this.$message(`请设置(${item.name})的活动规则`)
                return
              }

              let itemMesg = {
                activityStartTime: this.addParamMesg.activityStartDate.split(' ')[1],
                activityEndTime: this.addParamMesg.activityEndDate.split(' ')[1],
                activityDiscounts: item.activityDiscounts,
                activityGoodsName: item.name,
                curStock: item.curStock,
                platformCityGoodsId: item.id,
                sortNum: '',
                activityStartDate: this.addParamMesg.activityStartDate,
                activityEndDate: this.addParamMesg.activityEndDate,
                activityId: this.addParamMesg.activityId,
                categoryCode: item.categoryNo,
                categoryName: item.categoryNoName,
                goodsNo: item.goodsNo,
                limitGoodsCount: item.limitGoodsCount,
                platformCityGoodsName: item.name,
                spec: item.spec
              }
              this.addParamMesg.platCityGoodsInfos.push(itemMesg)
            }
          }
        }

        if(this.addParamMesg.activityStoreList.length === 0) {
          this.$message("请选择参与门店")
          return
        }
        if(this.addParamMesg.platCityGoodsInfos.length === 0) {
          this.$message("请选择参与商品")
          return
        }

        this.isDisabled = true
        let requestUrl = ''
        if(this.optType === 'edit') {
          requestUrl = reqUrl.updateDiscount
        }else if(this.optType === 'copy') {
          requestUrl = reqUrl.addDiscount
          delete this.addParamMesg.activityId
          delete this.addParamMesg.id
        }else {
          this.$message("操作类型不匹配")
          return
        }
        this.$http.post(requestUrl, {...this.addParamMesg}).then(res => {
          // 保存按钮可再次点击
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("修改成功")
            this.$router.push('/store_act')
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      doCancelHandel() {
        this.$confirm('确定取消吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/store_act')
        }).catch(() => {})
      },
      /**
       * [chooseStoreOpen 选择门店  线上]
       * @return {[type]} [description]
       */
      chooseStoreOpen() {
        let _params = {
          title: '选择门店',
          platformId: this.addParamMesg.platformId,
          regionCode: this.addParamMesg.regionCode,
          checkedList: this.activityStoresCheckedList
        }
        this.$refs.chooseStore.setDefault(_params)
      },
      /**
       * [chooseGoodsOpen 选择商品  线上]
       * @return {[type]} [description]
       */
      chooseGoodsOpen() {
        if(!this.activityStoresCheckedList || this.activityStoresCheckedList.length <=0) {
          this.$message.warning("请先选择参与门店")
          return
        }
        let storeNos = this.activityStoresCheckedList.map((item, index, arr) => {
          return item.erpStoreNo
        })
        let _params = {
          title: '选择商品',
          regionCode: this.addParamMesg.regionCode,
          checkedList: this.activityGoodsCheckedList,
          storeNos: storeNos
        }
        this.$refs.chooseGoods.setDefault(_params)
      },
      /**
       * [confirmChooseStore 选中的门店 线上]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      confirmChooseStore(_value) {
        let activityStoresCheckedList = []
        if(_value.checkedList && _value.checkedList.length) {
          activityStoresCheckedList = _value.checkedList
        }else {
          activityStoresCheckedList = []
        }
        this.activityStoresCheckedList = activityStoresCheckedList
      },
      /**
       * [confirmChooseStore 选中的商品 线上]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      confirmChooseGoods(_value) {
        let activityGoodsCheckedList = []
        if(_value.checkedList && _value.checkedList.length) {
          activityGoodsCheckedList = _value.checkedList
        }else {
          activityGoodsCheckedList = []
        }
        this.activityGoodsCheckedList = activityGoodsCheckedList
      },
      /**
       * [deleteStoreItem 删除选中的门店]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteStoreItem(_row, _shopType) {
        let shopType = _shopType
        if(shopType == 1) {
          let erpStoreNo = _row.erpStoreNo
          this.activityStoresCheckedList = this.activityStoresCheckedList.filter(item => {
            return item.erpStoreNo !== erpStoreNo
          })
        }else if(shopType == 2) {
          let storeNo = _row.storeNo
          this.offlineStoresCheckedList = this.offlineStoresCheckedList.filter(item => {
            return item.storeNo !== storeNo
          })
        }
      },
      /**
       * [deleteItem 删除选中的商品]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteGoodsItem(_row, _shopType) {
        let shopType = _shopType
        if(shopType == 1) {
          let platformCityGoodsId = _row.platformCityGoodsId
          this.activityGoodsCheckedList = this.activityGoodsCheckedList.filter(item => {
            return item.platformCityGoodsId !== platformCityGoodsId
          })
        }else if(shopType == 2) {
          let goodsNo = _row.goodsNo
          this.offlineGoodsCheckedList = this.offlineGoodsCheckedList.filter(item => {
            return item.goodsNo !== goodsNo
          })
        }
      },

      /**
       * [chooseOfflineStoreOpen 选择门店 线下]
       * @return {[type]} [description]
       */
      chooseOfflineStoreOpen() {
        let _params = {
          title: '选择门店',
          chooseStoreTOF: true,
          checkedList: this.offlineStoresCheckedList
        }
        this.$refs.chooseOfflineStore.setDefault(_params, 'noExclude')
      },
      /**
       * [chooseOfflineGoodsOpen 选择商品 线下]
       * @return {[type]} [description]
       */
      chooseOfflineGoodsOpen(_value) {
        // 选择门店商品
        let _params = {
          title: '选择门店商品',
          checkedList: this.offlineGoodsCheckedList
        }
        this.$refs.chooseOfflineGoods.setDefault(_params, false, 'noExclude')
      },
      /**
       * [confirmChooseOfflineGoods 选中的商品 线下]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      confirmChooseOfflineGoods(_value) {
        if(_value.checkedList && _value.checkedList.length) {
          this.offlineGoodsCheckedList = _value.checkedList
        }else {
          this.offlineGoodsCheckedList = []
        }
      },
      /**
       * [confirmChooseStore 选中的门店 线下]
       * @param  {[type]} _value [description]
       * @return {[type]}        [description]
       */
      confirmChooseOfflineStore(_value) {
        if(_value.checkedList && _value.checkedList.length) {
          this.offlineStoresCheckedList = _value.checkedList
        }else {
          this.offlineStoresCheckedList = []
        }
      },

      /**
       * [setDiscount 设置]
       */
      setDiscount(_row) {
        if(!_row.activityDiscounts || _row.activityDiscounts.length === 0) {
          let disItem = {
            amount: '', // 包装折扣金额
            qty: '' // 包装折扣数量
          }
          _row.activityDiscounts = []
          _row.activityDiscounts.push(disItem)
        }
        this.setItemDiscountMsg = JSON.parse(JSON.stringify(_row)) 

        this.setDisDialogShow = true
      },
      /**
       * [addDisItems 添加折扣条目]
       * @param {[type]} _idx [description]
       */
      addDisItems(_idx) {
        let setItemDiscountMsgTemp = this.setItemDiscountMsg
        let disItem = {
          amount: '', // 包装折扣金额
          qty: '' // 包装折扣数量
        }
        setItemDiscountMsgTemp.activityDiscounts.push(disItem)
        this.setItemDiscountMsg = JSON.parse(JSON.stringify(setItemDiscountMsgTemp))
      },
      /**
       * [removeDisItems 删除指定数组中的某一项]
       * @param  {[type]} _idx [description]
       * @return {[type]}      [description]
       */
      removeDisItems(_idx) {
        let setItemDiscountMsgTemp = this.setItemDiscountMsg
        setItemDiscountMsgTemp.activityDiscounts.splice(_idx, 1)
        this.setItemDiscountMsg = JSON.parse(JSON.stringify(setItemDiscountMsgTemp))
      },
      /**
       * [doSetDisSaveHandel 设置折扣保存]
       * @param  {[type]} _goodsNo [description]
       * @return {[type]}          [description]
       */
      doSetDisSaveHandel(_goodsNo) {
        let numReg = /^[1-9]*[1-9][0-9]*$/
        let salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
        let goodsNo = _goodsNo
        let activityDiscountsTemp = this.setItemDiscountMsg.activityDiscounts
        for(let item of activityDiscountsTemp) {
          if(!item.amount || !item.qty) {
            this.$message.warning("请输入活动规则中的份数和金额")
            return
          }
          if(!numReg.test(item.qty)) {
            this.$message.warning("活动规则中份数为大于0的正整数")
            return
          }
          if(!salePriceReg.test(item.amount)) {
            this.$message.warning("活动规则中金额为大于0，小数保留小数点后2位")
            return
          }
          let remainder = ((parseFloat(item.amount) * 100) / parseInt(item.qty)) / 100
          let remainderArr = remainder.toString().split('.')
          if(remainderArr[1] && remainderArr[1].length > 2) {
            this.$message.warning("该商品的价格无法被数量除尽，请重新填写。")
            return
          }
        }

        for(let i = 0; i < activityDiscountsTemp.length; i++) {
          for(let j = 0; j < activityDiscountsTemp.length; j++) {
            if(i != j) {
              if(parseFloat(activityDiscountsTemp[i].qty) === parseFloat(activityDiscountsTemp[j].qty)) {
                this.$message.warning("同一商品活动规则不允许件数相同")
                return
              }
              if(parseFloat(activityDiscountsTemp[i].amount) === parseFloat(activityDiscountsTemp[j].amount)) {
                this.$message.warning("同一商品活动规则不允许价格相同")
                return
              }
            }
          } 
        }

        let shopType = this.addParamMesg.shopType
        if(shopType == 1) {
          // 线上
          for(let item of this.activityGoodsCheckedList) {
            if(item.goodsNo === goodsNo) {
              item.activityDiscounts = this.setItemDiscountMsg.activityDiscounts
            }
          }
        }else if(shopType == 2) {
          // 线下
          for(let item of this.offlineGoodsCheckedList) {
            if(item.goodsNo === goodsNo) {
              item.activityDiscounts = this.setItemDiscountMsg.activityDiscounts
            }
          }
        }
        this.setDisDialogShow = false
      }

    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }
  }
</script>


